import React, { PureComponent, Suspense } from "react";
import Network from "../../Elements/Network/Network";
const AverageRating = React.lazy(() => import('../../Elements/AverageRating/AverageRating'));

export default class AverageRatingFixed extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            averageCalcBadge: 0,
        }
    }
    componentDidMount() {
        const { reviewFilters } = this.props;
        let totalReviewsSum = 0;
        let weightedSum = 0;

        for (const key in reviewFilters) {
            const network = reviewFilters[key].Network;
            totalReviewsSum += network.totalReviews;
            weightedSum += network.totalReviews * network.averageReviews;
        }

        if (totalReviewsSum > 0) {
            const averageCalcBadge = weightedSum / totalReviewsSum;
            this.setState({ averageCalcBadge });
        }
    }
    calculateSum = () => {
        const { reviewFilters } = this.props;
        let sumOfAll = 0;

        for (const key in reviewFilters) {
            if (reviewFilters.hasOwnProperty(key)) {
                const networkObj = reviewFilters[key].Network;
                sumOfAll += networkObj.totalReviews;
            }
        }

        return sumOfAll;
    };


    togglePopup = () => {
        this.props.togglePopup(false)
    }
    render() {
        const { ThemeRule, reviewFilters } = this.props
        const { averageCalcBadge } = this.state;
        const sumOfAll = this.calculateSum();
        let avgrate = parseFloat(averageCalcBadge).toFixed(1);

        return (<div className="tb_bt_post_wrap_in" onClick={this.togglePopup}>
            <div className="tb_bt_social_" role="list" aria-roledescription="social network list">
                {reviewFilters && reviewFilters.length > 0 ? reviewFilters.map((items, index) => {
                    const item = items.Network;
                    return index < 5 ? <div key={index} className="tb_bt_social_list" role="listitem" aria-roledescription={`network ${item.name} `}>
                        <Network
                            ThemeRule={ThemeRule}
                            networkClass={"tb_bt_avg_social_ico"}
                            network={item}
                            isDefault={true}
                        />
                    </div> : null
                }) : null}

            </div>
            <div className="tb_bt_rating_wrap">
                <div className="tb_bt_avg_rating" aria-label={`rating ${avgrate}`} >{avgrate}</div>
                <Suspense fallback={<></>}>
                    <AverageRating size={14} rating={avgrate} />
                </Suspense>
            </div>
            <div className="tb_bt_review_link" aria-label={`Read our {sumOfAll} reviews`}>Read our {sumOfAll} reviews</div>
        </div >
        );
    }
}
