import React, { PureComponent, Suspense } from "react";
import { isTagEmbed } from "../../../../actions/api";
const DefaultNetwork = React.lazy(() => import("./DefaultNetwork"));

export default class Network extends PureComponent {
  render() {
    const { network, networkClass, isDefault } = this.props;
    const isDefaultIcon = isDefault ? (network.id === 7 || network.id === 4 || network.id === 36) ? false : true : false
    const icon = isTagEmbed && network.id == 36 ? "tagembed" : network.icon;
    return (isDefaultIcon ? <div className={`${networkClass} tb__icon tb-${icon.replace(/fa-/g, "")}`} aria-label={`post network ${network?.name || ""}`}><div></div></div> : <Suspense fallback={<></>}> <DefaultNetwork network={network} networkClass={networkClass} /></Suspense>);
  }
}
